<template>
    <section class="item-wrapper">

        <div class="item" v-on:click="clickHandler">
            <p>{{name}}</p>
            <i v-if="!isChildrenVisible && hasChilren" class="fas fa-caret-down"></i>
            <i v-if="isChildrenVisible && hasChilren" class="fas fa-caret-up"></i>
        </div>

        <section class="children" v-if="hasChilren && isChildrenVisible">

            <ExpandableListItem v-for="item in children" :key="item.id"
                                v-bind:name="item.name" v-bind:children="item.children"
                                v-bind:id="item.id" @choose="onChoose">

            </ExpandableListItem>

        </section>
    </section>
</template>

<script>
    export default {
        name: "ExpandableListItem",

        props: {
            id: {
                type: String,
                required: true,
            },

            name: {
                type: String,
                require: true,
            },

            children: {
                type: Array,
                required: false,
            },

            isExpanded: {
                type: Boolean,
                required: false,
            }
        },

        data: () => {
            return {
                isChildrenVisible: false,
            }
        },

        methods: {

            clickHandler() {
                if (this.children) {
                    this.isChildrenVisible = !this.isChildrenVisible;
                    this.$emit('expand', this.id);
                } else {
                    this.$emit('choose', {id: this.id, name: this.name})
                }
            },

            onChoose(item) {
                //console.log("item:", item);
                item.parentId = this.id;
                this.$emit('choose', item);
            },
        },

        computed: {
            hasChilren: function () {
                return this.children && this.children.length !== 0
            }
        },

        mounted() {
            if (this.isExpanded !== null && this.isExpanded !== undefined) {
                this.isChildrenVisible = this.isExpanded;
            }
        }
    }
</script>

<style lang="sass" scoped>

    .item-wrapper
        border-bottom: 2px solid #E0E0E0

        &:last-child
            border-bottom: none

        .item
            cursor: pointer
            display: inline-flex
            width: 100%
            padding: 10px 0 0
            transition: 0.35s
            justify-content: flex-end

            p
                margin-right: auto

            i
                margin-top: 1rem
                margin-bottom: 1rem

            &:hover
                background-color: #F5F5F5

        .children
            .item-wrapper
                border-bottom: none
                //border: 1px solid red

            .item p
                margin-left: 15px
</style>