<template>
    <div class="basket-wrapper">
        <div class="basket" v-if="isBasketWindowVisible" id="basket" v-bind:class="{ 'basket-minimized': basketWindow.isMinimized}">

            <div class="top-bar">

                <!--<div class="left">
                    <p class="title">: {{basketTotalPrice}}</p>

                    <button v-if="isBasketValid && basketTotalPrice > 0 && canBeOrdered" v-on:click="$emit('ordering')"></button>
                </div>-->

                <i class="far fa-window-minimize clickable" v-if="!basketWindow.isMinimized" v-on:click="basketWindow.isMinimized = true"></i>
                <i class="fas fa-window-minimize clickable" v-if="basketWindow.isMinimized" v-on:click="basketWindow.isMinimized = false"></i>
                <!--<i class="fas fa-expand clickable"></i>-->
                <i class="fas fa-times clickable" v-on:click="closeBasket"></i>
            </div>

            <ProductsTable @remove="$emit('remove', $event)" @quantityChange="onQuantityChange"
                           v-bind:data="data" :is-editable="true"
                           :is-full-size="false" :height="239">
            </ProductsTable>

            <div class="bottom">
                <p class="totalSum">: <span>{{basketTotalPrice}}</span></p>

                <button v-if="isBasketValid && basketTotalPrice > 0 && canBeOrdered" class="right" v-on:click="$emit('ordering')"></button>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapMutations } from 'vuex';
    import modules from "../../../store/all.modules.name"
    import mutations from "../../../store/all.mutations.type";
    import ProductsTable from "../ProductsTable";

    export default {
        name: "Basket",

        components: {ProductsTable},

        props: {
            data: {
                type: Array,
                required: true,
            },

            canBeOrdered: {
                type: Boolean,
                default: true,
            },
        },

        data: () => {
            return {
                basketWindow: {
                    isMinimized: false,
                    height: null,
                }
            }
        },

        methods: {
            ...mapMutations(modules.catalog, [
                mutations.catalog.REMOVE_FROM_BASKET,
                mutations.catalog.CLEAR,
            ]),

            onQuantityChange(payload) {
                let {item, newQuantity} = payload;
                item.boughtQuantity = newQuantity;
            },

            closeBasket() {
                this.clear();
            },
        },

        computed: {

            isBasketWindowVisible: function () {
                return this.data.length !== 0
            },

            isBasketValid: function () {
                let basket = this.data;

                for (let i = 0; i < basket.length; i++) {
                    if (!basket[i].isValid || !basket[i].boughtQuantity) {
                        return false;
                    }
                }

                return true;
            },

            basketTotalPrice: function() {

                if (this.isBasketValid) {
                    let res = 0,
                        basket = this.data;

                    for (let i = 0; i < basket.length; i++) {
                        res += basket[i].price * basket[i].boughtQuantity;
                    }

                    return Math.round(res * 1000) / 1000;
                } else {
                    return -1;
                }
            },
        },

        created() {
            /*if (mySessionStorage.has(storageKey)) {
                this.basket = mySessionStorage.getObject(storageKey);
            }*/
        },

        destroyed() {
            /*if (this.basket.length !== 0)
                mySessionStorage.addObject(storageKey, this.basket);*/
        },
    }
</script>

<style lang="sass" scoped>
    @import '../../../assets/basic'

    .basket-wrapper
        width: 90%
        max-width: 1200px
        position: fixed
        bottom: 0
        left: 0
        right: 0
        margin-left: auto
        margin-right: auto

        .basket-minimized
            height: 21px!important

        .basket
            position: absolute
            overflow: hidden
            width: 450px
            height: 300px
            transition: 0.5s
            right: 0
            bottom: 0
            background-color: $background-color
            box-shadow: 0 0 6px rgba(0,0,0,0.5)
            border-radius: 5px 5px 0 0
            font-family: Roboto, sans-serif

            /*@include large
                width: 50%*/

            .top-bar
                width: 100%
                display: flex
                justify-content: flex-end
                background-color: $secondary-color
                color: $background-color
                border-radius: 5px 5px 0 0

                .title
                    //margin: 2.5px auto 2.5px 5px

                .left
                    margin: 0.15rem auto 0.15rem 0.5rem

                    p
                        display: inline-block
                        margin: 0

                    button
                        display: inline-block
                        margin-left: 1rem
                        background-color: $secondary-color
                        border-radius: 3px
                        font-size: 0.9rem
                        border: 1px solid $background-color
                        color: $background-color
                        cursor: pointer

                i
                    margin-right: 15px
                    margin-top: 2.5px
                    margin-bottom: 2.5px


            .table-wrapper
                overflow-y: auto
                height: 239px

            .bottom
                display: flex
                padding: 5px
                border-top: 2px solid #777777

                p
                    margin-top: auto
                    margin-bottom: auto
                    padding: 2px 5px
                    border-radius: 5px
                    background-color: $secondary-color
                    color: $background-color

                button
                    display: inline-block
                    height: 30px
                    margin-left: 1rem
                    background-color: $secondary-color
                    border-radius: 3px
                    font-size: 0.9rem
                    border: 1px solid $background-color
                    color: $background-color
                    cursor: pointer

                .right
                    margin-left: auto
</style>