<template>

    <div class="expandable-list">
        <ExpandableListItem v-for="item in data" :key="item.id"
                            v-bind:id="item.id" v-bind:name="item.name"
                            v-bind:children="item.children" v-bind:is-expanded="item.isExpanded"
                            @choose="onChoose" @expand="onExpand">
        </ExpandableListItem>
    </div>

</template>

<script>
    import ExpandableListItem from "./ExpandableListItem";

    export default {
        name: "ExpandableList",

        components: {ExpandableListItem},

        props: {
            data: {
                type: Array,

                require: true,
            }
        },

        methods: {
            onChoose(item) {
                this.$emit('choose', item);
            },

            onExpand(item) {
                this.$emit('expand', item);
            },
        },
    }
</script>

<style lang="sass" scoped>
    .expandable-list
        display: block
        font-family: Roboto, sans-serif
</style>