<template>
    <section>

        <div class="table" v-bind:class="{'editable-numbering': isEditable && hasNumbering, 'editable': isEditable && !hasNumbering}">

            <div class="head" v-bind:style="{'margin-right': isBodyOverflowed? '15px': ''}">

                <div class="row" v-if="isFullSize">
                    <p v-if="hasNumbering" class="numbering-column">#</p>
                    <p class="name-column"></p>
                    <p class="color-column"></p>
                    <p class="size-column"></p>
                    <p class="quantity-column">.</p>
                    <p class="price-column"></p>
                    <p v-if="isEditable" class="trash-column"></p>
                </div>
                <div class="row" v-else>
                    <p v-if="hasNumbering" class="numbering-column">#</p>
                    <p class="name-column"></p>
                    <p class="color-column"></p>
                    <p class="size-column">.</p>
                    <p class="quantity-column">.</p>
                    <p class="price-column"></p>
                    <p v-if="isEditable" class="trash-column"></p>
                </div>
            </div>

            <div class="body" v-bind:style="{'height': height === -1? '' : (height - 35) + 'px'}" ref="body">

                <div class="row" v-for="(item, index) in data" :key="item.id">
                    <p class="numbering-column" v-if="hasNumbering">{{index + 1}}</p>

                    <p class="name-column" v-bind:class="{clickable: item.hasImage}" v-on:click="imagePreview(item)">{{item.name}}</p>

                    <p class="color-column" v-bind:class="{clickable: item.hasImage, small: !isFullSize}"
                        v-on:click="imagePreview(item)">{{item.color}}</p>

                    <p class="size-column">{{item.size}}</p>

                    <div class="quantity-column">
                        <input type="number" min="1" :max="item.quantity" :value="item.boughtQuantity"
                               v-if="isEditable" @input="quantityPickerInputHandler($event, item)"/>

                        <span v-else>{{item.boughtQuantity}}</span>
                    </div>

                    <p class="price-column">
                        {{item.price}}
                    </p>

                    <div class="trash-column" v-if="isEditable">
                        <img src="/svgs/trash.svg" v-on:click="remove(index)" class="trash clickable" />
                    </div>
                </div>

            </div>

        </div>

        <div class="bottom" v-if="isFullSize"></div>
    </section>
</template>

<script>
    import imageFetch from "../../common/imageFetch"
    import {EventBus, Events} from "../../common/eventBus";

    export default {
        name: "ProductsTable",

        props: {
            data: {
                type: Array,
                required: true
            },

            height: {
                type: Number,
                default: -1,
            },

            isFullSize: {
                type: Boolean,
                default: true
            },

            isEditable: {
                type: Boolean,
                default: true,
            },

            hasNumbering: {
                type: Boolean,
                default: false,
            }
        },

        data: () => {
            return {
                isBodyOverflowed: false,
                animationFrame: null,
            }
        },

        methods: {
            remove(index) {
                this.$emit("remove", index);
            },

            quantityPickerInputHandler($event, item) {
                if (event.target.value < 0) {
                    event.target.value = 0;
                }

                //item.boughtQuantity = parseInt(event.target.value);
                this.$emit("quantityChange", {item, newQuantity: parseInt(event.target.value)})
            },

            imagePreview(item) {
                if (item.hasImage) {
                    EventBus.$emit(Events.showImagePreview, imageFetch.fullSizeImageURL(item.id));
                }
            },
        },

        mounted() {
            if (this.height !== -1) {
                const observer = () => {
                    if (this.animationFrame) {
                        cancelAnimationFrame(this.animationFrame)
                    }

                    if (this.$refs.body) {
                        this.isBodyOverflowed = this.$refs.body.scrollHeight > this.$refs.body.clientHeight;
                    }

                    this.animationFrame = requestAnimationFrame(observer);
                };

                observer();
            }
        },
    }
</script>

<style lang="sass" scoped>
    .clickable
        cursor: pointer

    .editable
        .row
            grid-template-columns: minmax(120px, 1fr) minmax(80px, 0.4fr) 0.3fr minmax(50px, 0.2fr) 0.4fr 24px!important

    .editable-numbering
        .row
            grid-template-columns: 20px minmax(120px, 1fr) minmax(80px, 0.4fr) 0.3fr minmax(50px, 0.2fr) 0.4fr 24px!important

    .table

        .row
            display: grid
            grid-template-columns: 20px minmax(120px, 1fr) minmax(80px, 0.4fr) 0.3fr minmax(50px, 0.2fr) 0.4fr

            p, div
                overflow: hidden
                padding: 8px 5px

            p
                margin: auto 0
                word-wrap: break-word
                text-align: left

            div
                display: flex
                align-items: center

        .head

            p
                font-weight: bold

        .body
            overflow-x: hidden
            overflow-y: auto

            .row
                transition: 0.3s

                &:hover
                    background-color: #f2f2f2


            .quantity-column
                input
                    width: 40px
                    background-color: #ffffff00
                    border: 0
                    outline: none

            .trash-column

                .trash
                    width: 14px
                    height: 14px
</style>