<template>
    <section class="layout">
        <ExpandableList  v-if="categories.length !== 0" v-bind:data="categories"
                         @choose="onCategoryChoose" @expand="onCategoryExpand">
        </ExpandableList>

        <Basket v-bind:data="!isComponent ? basket: data"
                v-bind:can-be-ordered="!isComponent" @ordering="onBasketOrdering"
                @remove="onBasketRemove">
        </Basket>
    </section>
</template>

<script>
    import { mapGetters, mapActions, mapMutations } from 'vuex';
    import actions from "../store/all.actions.type";
    import mutations from "../store/all.mutations.type";
    import modules from "../store/all.modules.name"

    import ExpandableList from "./components/expandableList/ExpandableList";
    import Basket from "./components/catalog/Basket";

    export default {
        name: "CategoryChoose",

        components: {Basket, ExpandableList},

        props: {
            isComponent: {
                type: Boolean,
                default: false,
            },

            data: {
                type: Array,
                default: null,
            },
        },

        methods: {
            ...mapActions(modules.catalog, [
                actions.catalog.LOAD_CATEGORIES,
            ]),

            ...mapMutations(modules.catalog, [
                mutations.catalog.REMOVE_FROM_BASKET,
            ]),

            onCategoryChoose(val) {
                if (!this.isComponent) {

                    if (val.parentId) {
                        this.$router.push({ path: `/catalog/${val.parentId}/${val.id}` });
                    } else {
                        this.$router.push({ path: `/catalog/${val.id}` });
                    }

                } else {
                    this.$emit('choose', {category: val.id, parentCategory: val.parentId})
                }
            },

            onCategoryExpand(val) {
                if (!this.isComponent) {
                    this.$store.commit(modules.catalog + "/" + mutations.catalog.CHOOSE_CATEGORY, val);
                }
            },

            onBasketRemove(index) {
                if (!this.isComponent) {
                    this.removeFromBasket(index);
                } else {
                    this.$emit('basketRemove', index)
                }
            },

            onBasketOrdering() {
                this.$router.push({name: "ordering"});
            },
        },

        computed: {
            ...mapGetters(modules.catalog, [
                "basket",
                "categories",
                "chosenCategory"
            ]),
        },

        created() {
            if (this.categories.length === 0) {
                this.loadCategories();
            }
        },
    }
</script>

<style lang="sass" scoped>
    .layout
        width: 90%
        margin: auto
        max-width: 500px
        font-family: Roboto, sans-serif
</style>